body {
  font: 14px "Century Gothic", Futura, sans-serif;
  margin: 20px;
}

ol,
ul {
  /* padding-left: 30px; */
}

/* changed logic inside render of getMoves to do this without className */
.move:focus {
  font-weight: bold;
}

.game-board {
  box-shadow: rgba(0, 0, 0, 0.35) 0px 5px 15px;
  padding: 10px;
  border-radius: 10px;
  height: max-content;
  margin-top: 10px;
}

.game-moves {
  box-shadow: rgba(0, 0, 0, 0.35) 0px 5px 15px;
  /* padding: 0px; */
  margin: 1px;
  border-radius: 5px;
  /* height: max-content; */
}

.board-row:after {
  clear: both;
  content: "";
  display: table;
}

.status {
  margin-bottom: 10px;
}

.square {
  background: #fff;
  border: 1px solid #999;
  float: left;
  font-size: 24px;
  font-weight: bold;
  line-height: 34px;
  height: 34px;
  margin-right: -1px;
  margin-top: -1px;
  padding: 0;
  text-align: center;
  width: 34px;
}

.square:focus {
  outline: none;
}

.kbd-navigation .square:focus {
  background: #ddd;
}

.game {
  display: flex;
  flex-direction: row;
  min-width: max-content;
}

.game-info {
  margin-left: 30px;
}

.game-info > ul {
  list-style-type: none;
  padding-left: 0px;
  border-radius: 10px;
  margin-top: 1;
}

@media (max-width: 480px) {
  .game {
    flex-direction: column;
  }
  .game-board {
    width: max-content;
  }
  .game-info {
    margin-left: 0;
    margin-top: 30px;
  }
}
